<template>
  <div class="writes">
    <v-container>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step
            :complete="e1 > 1"
            step="1"
            color="pink lighten-3"
          >
            꽃을 골라주세요
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step
            :complete="e1 > 2"
            step="2"
            color="yellow lighten-1"
          >
            닉네임을 입력해주세요
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step 
            step="3"
            color="light-green darken-2"
          >
            편지를 남겨주세요
          </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <v-row justify="center">
              <div class="pt-3">
                <h2>꽃💐을 골라주세요</h2>
              </div>
            </v-row>
            <v-row>
              <v-col cols="4" justify="center" @click="selectRed">
                <img
                  src="@/assets/red.png"
                  width="90%"
                />
              </v-col>
              <v-col cols="4" justify="center" @click="selectOrange">
                <img 
                  src="@/assets/orange.png"
                  width="90%"
                />
              </v-col>
              <v-col cols="4" justify="center" @click="selectPink">
                <img
                  src="@/assets/pink.png"
                  width="90%" 
                />
              </v-col>
            </v-row>

            <v-row justify="center">
              <img v-if="selectedFlower==1" height="50%" src="@/assets/red.png">
              <img v-else-if="selectedFlower==2" width="95%" src="@/assets/orange.png">
              <img v-else-if="selectedFlower==3" height="50%" src="@/assets/pink.png">
            </v-row>

            <v-row justify="center" class="pt-2">
              <div v-if="isSelected==true">{{ languages[selectedFlower] }}</div>
              <div v-if="isSelected==true">{{ messages[selectedFlower] }}</div>
            </v-row>

            <v-row class="pa-3"></v-row>
            
            <v-btn
              plain
              @click="next"
              color="green darken-3"
            >
              다음
            </v-btn>

            <v-btn 
              plain
              color="red"
              @click="goToHome"
            >
              취소
            </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-row justify="center">
              <img v-if="selectedFlower==1" height="50%" src="@/assets/red.png">
              <img v-else-if="selectedFlower==2" width="95%" src="@/assets/orange.png">
              <img v-else-if="selectedFlower==3" height="50%" src="@/assets/pink.png">
            </v-row>

            <v-row justify="center" class="pt-2 px-3">
              <v-text-field
                label="닉네임을 입력해주세요"
                outlined
                v-model="writer"
              ></v-text-field>
            </v-row>

            <v-btn
              plain
              @click="next"
              color="green darken-3"
            >
              다음
            </v-btn>

            <v-btn 
              plain
              @click="e1 = 1"
              color="red"
            >
              취소
            </v-btn>
          </v-stepper-content>

          <v-stepper-content step="3">
            <v-row justify="center">
              <v-col cols="4">
                <v-avatar size="150">
                  <v-img v-if="selectedFlower==1" src="@/assets/red.png"></v-img>
                  <v-img v-else-if="selectedFlower==2" src="@/assets/orange.png"></v-img>
                  <v-img v-else-if="selectedFlower==3" src="@/assets/pink.png"></v-img>
                </v-avatar>
              </v-col>
              <v-col cols="8" align-self="center">
                작성자 : {{ writer }}
              </v-col>
            </v-row>

            <v-row justify="center" class="pt-2 px-3">
              <v-textarea
                outlined
                label="메시지를 입력해주세요"
                v-model="message"
              ></v-textarea>
            </v-row>

            <v-btn
              plain
              @click="writeLetter"
              color="green darken-2"
            >
              등록
            </v-btn>

            <v-btn 
              plain
              @click="e1 = 2"
              color="red"
            >
              이전
            </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-container>
  </div>
</template>

<script>
import firestore from '../main'
import { doc, collection, setDoc, serverTimestamp } from 'firebase/firestore'

export default {
  name : 'WriteLetter',
  data() {
    return {
      e1: 1,
      isSelected: false,
      selectedFlower: 0,
      languages: {
        1: '선택하신 빨간 거베라 꽃의 꽃말은 사랑입니다.',
        2: '선택하신 주황 거베라 꽃의 꽃말은 감사입니다.',
        3: '선택하신 분홍 거베라 꽃의 꽃말은 모험 정신입니다.'
      },
      messages: {
        1: '편지를 남겨주시는 여러분 사랑해요~ 🫶🏻',
        2: '아빠의 퇴직을 해주셔서 감사해요! 🤗',
        3: '저희 아빠의 새출발을 응원해주세요! 🙌🏻'
      },
      writer: null,
      message: null
    }
  },
  components: {
  },
  methods: {
    goToHome() {
      this.$router.push('/')
    },
    selectRed() {
      this.isSelected = true
      this.selectedFlower = 1
    },
    selectOrange() {
      this.isSelected = true
      this.selectedFlower = 2
    },
    selectPink() {
      this.isSelected = true
      this.selectedFlower = 3
    },
    next() {
      if (this.e1 == 1 && this.selectedFlower < 1) {
        alert("꽃을 골라주세요")
      } else if (this.e1 == 2 && this.writer == null) { 
        alert("닉네임을 입력해주세요")
      } else {
        this.e1++
      }
    },
    writeLetter() {
      if (this.e1 == 3 && this.message == null) {
        alert("메시지를 입력해주세요")
      }
      
      const docData = {
        f: this.selectedFlower,
        w: this.writer,
        m: this.message,
        t: serverTimestamp()
      }

      const letterId = doc(collection(firestore, "letters"))

      setDoc(letterId, docData)
        .then(() => { this.$router.push('/hyesun')})

    }
  }
}
</script>

<style>
  .writes {
    font-family: 'GangwonEdu_OTFBoldA';
  }
</style>